import React from "react";
import errorImg from "../../../Assets/icons/error.svg";
import { reload } from "../../../Utils/utils";

//     

const Error = () => {
  return (
    <div className="error">
      <img className="error__img" src={errorImg} alt="error" />
      <div className="error__title">-   </div>
      <div className="error__subtitle">  </div>
      <div className="error__refresh" onClick={reload}>
         
      </div>
    </div>
  );
};

export default Error;
