import React from "react";
import close from "../../Assets/icons/close.svg";

//      

const ModalFilter = ({ switchActiv, switchSort, sortUser }) => {
  //  
  let setSortFirstName = () => {
    switchSort("firstName");
    switchActiv(false);
  };
  //   
  let setSortBirtday = () => {
    switchSort("birthday");
    switchActiv(false);
  };
  return (
    <div className="modal">
      <div className="modal__title"></div>
      <div className="modal__close">
        <button onClick={() => switchActiv(false)}>
          <img src={close} alt="close" />
        </button>
      </div>
      <div className="modal__body">
        <div
          className={
            sortUser === "firstName"
              ? "modal__btm modal__btm_active"
              : "modal__btm"
          }
          onClick={() => setSortFirstName()}
        >
          <label> </label>
        </div>
        <div
          className={
            sortUser === "birthday"
              ? "modal__btm modal__btm_active"
              : "modal__btm"
          }
          onClick={() => setSortBirtday()}
        >
          <label>  </label>
        </div>
      </div>
    </div>
  );
};

export default ModalFilter;
